import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOptionsPsComplexCallSlot } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-call-slot/ps-complex-call-slot.component.interfaces';
import { IOptionsPsComplexInappUrlComponent } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-inapp-url/ps-complex-inapp-url.component.interface';
import { IOptionsPsComplexMediaCaptureComponent } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-media-capture/ps-complex-media-capture.component.interface';
import { IOptionsPsComplexSpecimenExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-specimen/ps-complex-specimen.component.interface';
import { IntegrationConstants } from 'src/app/commonBussinessSRC/psServices/integration/ps-integration-constants';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IDynamicComponentPageLoader, IOptionsPsTemplateForm } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { OmniBasePage } from '../omni-base/omni-base.page';

@Component({
  selector: 'dynamic-component-page-loader',
  templateUrl: './dynamic-component-page-loader.page.html',
})
export class DynamicComponentPageLoaderPage extends OmniBasePage implements OnInit {

  component;
  formGroup: FormGroup = new FormGroup({});

  public options: IOptionsPsTemplateForm = {
    group: this.formGroup,
    submitOptions: {
      group: this.formGroup,
      submitServiceUrl: IntegrationConstants.JBPM.serviceUrl.completeBpmTaskFromInterceptor,
      postCallFunction: {
        func() {
          return new Promise<any>((resolve, reject) => {
            this.executionClass.commonProv.events.publish('draft:refresh');
            resolve(true);
          });
        },
        executionClass: this,
        params: []
      },
    },
  };

  types = IDynamicComponentPageLoader;
  constructor(public navService?: PsNavigatorService) {
    super();
  }

  callSlotOptions: IOptionsPsComplexCallSlot = {
    group: this.formGroup,
    fcName: 'call_schedule',
    labelKey: 'call_schedule_key',
  }

  specimenOptions: IOptionsPsComplexSpecimenExposed = {
    group: this.formGroup,
    fcName: 'specimen',
    labelKey: 'specimen_key'
  };

  selfieOptions: IOptionsPsComplexMediaCaptureComponent = {
    group: this.formGroup,
    fcName: 'selfie',
    labelKey: 'selfie_key',
    mediaButton: {
      group: this.formGroup,
      labelKey: 'selfie_key'
    }
  }

  inAppUrlOptions: IOptionsPsComplexInappUrlComponent = {
    url: null,
  }

  ngOnInit() {
    const params = this.navService.getAllParams();
    const component = CommonUtils.valFromNestedObj('JBPM.screenMarkup.queryParams.componentName.MAP_VALUE', params)
    if (component) {
      this.component = component;
      this.options.submitOptions.extraParams = {
        ...this.options.submitOptions.extraParams,
        componentName: component
      }
      const queryParams = CommonUtils.valFromNestedObj('JBPM.screenMarkup.queryParams', params)
      if (component == this.types.CallSlot) {

      }

      if (component == this.types.Specimen) {

      }

      if (component == this.types.PSURL) {
        const url = queryParams['inapp-url.url'].MAP_VALUE;
        const type = queryParams['inapp-url.type'].MAP_VALUE;
        this.inAppUrlOptions.url = url;
        this.inAppUrlOptions.type = type;
      }

      if (component == this.types.Selfie) {

      }
    }
  }

}
